let currArea = 'empty', prevArea = 'empty',
    currElement = 'empty', prevElement = 'empty';

document.addEventListener('mousedown', e => {
    e = e || window.event;
    let target = e.target || e.srcElement,
        img = target.textContent || target.innerText;

    currArea = e.target.parentNode;

    if (currArea !== prevArea) {
        prevArea.id = "none";
    }

    currArea.id = "top-img";
    prevArea = currArea;

    if (e.target.className === "scale-element") {

        currElement = e.target;

        if (currElement !== prevElement) {
            prevElement.id = "none";
        }

        currElement.id = "selected-img";
        prevElement = currElement;
    } else {
        prevElement.id = "none";
    }
}, false);

document.addEventListener('touchstart', e => {
    e = e || window.event;
    let target = e.target || e.srcElement,
        img = target.textContent || target.innerText;

    currArea = e.target.parentNode;

    if (currArea !== prevArea) {
        prevArea.id = "none";
    }

    currArea.id = "top-img";
    prevArea = currArea;

    if (e.target.className === "scale-element") {

        currElement = e.target;

        if (currElement !== prevElement) {
            prevElement.id = "none";
        }

        currElement.id = "selected-img";
        prevElement = currElement;
    } else {
        prevElement.id = "none";
    }
}, false);